
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { ReplaySubject } from 'rxjs';
import { map } from 'rxjs/operators';
import { LoginReturn } from '../../_model/login';

import { HttpService } from '../http/http.service';

@Injectable({
  providedIn: 'root'
})
export class AccountService {
  baseUrl = '';
  user: LoginReturn;
  private currentUserSource = new ReplaySubject<LoginReturn>(1);
  currentUser$ = this.currentUserSource.asObservable();
  controller: string = 'Account/';

  constructor(private http: HttpService, private router: Router) { }

  login(model: any) {
    return this.http.httpPost(this.controller + 'login', model).pipe(
      map((user: LoginReturn) => {
        if (user) {
          console.log(user);
          this.setCurrentUser(user);
        }
      })
    );
  }

  logout() {
    localStorage.removeItem('user');
    this.currentUserSource.next(null);
    this.router.navigateByUrl('/auth/signin-v2');
  }

  setCurrentUser(user: LoginReturn) {
    localStorage.setItem('user', JSON.stringify(user));
    this.currentUserSource.next(user);
    this.getCurrentUser();
  }
  // ResetPassword(email: string) {
  //     return this.http.httpPost(this.Appcontroller + 'reset-password-token/' + email);
  // }
  // ChangePassword(model: ResetPassword) {
  //     return this.http.HttpPost(this.Appcontroller + 'reset-passwordd', model);
  // }

  getCurrentUser() {
    const user: LoginReturn = JSON.parse(localStorage.getItem('user'));
    this.user = user
    this.currentUserSource.next(user);

    if (user.appUserType.name_en == "Student") {
      if (!!user) {
        user.accepted === false ? this.router.navigateByUrl('/maintenance/offline-ui') : this.router.navigateByUrl('/membership/user-profile');
      } else {
        this.router.navigateByUrl('/auth/signin-v2');
      }
    } else {
      if (!user) {
        this.router.navigateByUrl('/auth/signin-v2');
      } else {
        this.router.navigateByUrl('/panel/dashboard');
      }
    }



  }
  getCurrentUserWithoutRouting() {
    const user: LoginReturn = JSON.parse(localStorage.getItem('user'));
    this.user = user
  }

  roleMatch(allowedRoles): boolean {
    let isMatch = false;
    if (!this.user) {
      this.router.navigateByUrl('/auth/signin-v2');
    } else {
      let userRoles: Array<string>;
      this.currentUser$.subscribe(data => { userRoles = data?.permissions as Array<string> });
      allowedRoles.forEach(element => {
        if (userRoles.includes(element)) {
          isMatch = true;
          return;
        }
      });
      return isMatch;
    }

  }

  GetPermissions() {
    return this.http.httpGet(this.controller + "GetPermissions");
  }
}
